<template>
  <div>
    <h2>Checkbox </h2>
    <h3></h3>
    <owl-code-example :code="code1">
      <p class="subtitle"></p>
      <owl-checkbox name="favorite" value="sports" v-model="favorite"></owl-checkbox>
      <owl-checkbox name="favorite" value="fitness" v-model="favorite"></owl-checkbox>
      <owl-checkbox name="favorite" value="badminton" v-model="favorite"></owl-checkbox>
      <p style="font-size:14px">{{JSON.stringify(favorite)}}</p>
    </owl-code-example>
    <h3>Checkbox Group</h3>
    <owl-code-example :code="code2">
      <p class="subtitle"></p>
      <owl-checkbox-group v-model="fruite" direction="vertical">
        <owl-checkbox value="apple"></owl-checkbox>
        <owl-checkbox value="origin"></owl-checkbox>
        <owl-checkbox value="tangeine"></owl-checkbox>
        <owl-checkbox value="banana"></owl-checkbox>
      </owl-checkbox-group>
      <p style="font-size:14px">{{JSON.stringify(fruite)}}</p>
    </owl-code-example>
    <h3>Checkbox </h3>
    <owl-table :data="propsList" :columns="columns" />
    <h3>Checkbox group </h3>
    <owl-table :data="propsList1" :columns="columns" />
  </div>
</template>
<script>
export default {
  data(){
    return {
      favorite: [],
      fruite: [],
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'introduce'},
      ],
      propsList: [
        {name: 'name', types: 'string', introduce: 'input'},
        {name: 'label', types: 'string', introduce: 'scopescope'},
        {name: 'v-model', types: 'Array<any>', introduce: ''},
        {name: 'value', types: 'string', introduce: 'inputcheckboxvalue'},
      ],
      propsList1:[
        {name: 'v-model', types: 'Array<any>', introduce: ''},
        {name: 'direction', types: '"horizontal" | "vertical"', introduce: 'checkbox'},
      ],
      code1:`&lt;template&gt;
  <p></p>
  <owl-checkbox name="favorite" value="sports" v-model="favorite"></owl-checkbox>
  <owl-checkbox name="favorite" value="fitness" v-model="favorite"></owl-checkbox>
  <owl-checkbox name="favorite" value="badminton" v-model="favorite"></owl-checkbox>
  <p style="font-size:14px">{{JSON.stringify(favorite)}}</p>
&lt;/template&gt;
      `,
      code2: `&lt;template&gt;
  <p></p>
  <owl-checkbox-group v-model="fruite" direction="vertical">
    <owl-checkbox value="apple"></owl-checkbox>
    <owl-checkbox value="origin"></owl-checkbox>
    <owl-checkbox value="tangeine"></owl-checkbox>
    <owl-checkbox value="banana"></owl-checkbox>
  </owl-checkbox-group>
  <p style="font-size:14px">{{JSON.stringify(fruite)}}</p>
&lt;/template&gt;`
    }
  }
}
</script>