<template>
  <div class="collapse-page">
    <h2>Collapse </h2>
    <h3></h3>
    <owl-code-example :code="code" >
      <owl-collapse v-model="active">
        <owl-collapse-panel value="value1" header="1">
        </owl-collapse-panel>
        <owl-collapse-panel value="value2" header="2">
        </owl-collapse-panel>
      </owl-collapse>
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code1" >
      <owl-collapse v-model="active1">
        <owl-collapse-panel value="value1" header="">
          <p  style="margin: 0; font-size: 14px;"></p>
        </owl-collapse-panel>
        <owl-collapse-panel value="value2" header="">
          <p  style="margin: 0; font-size: 14px;">-->-->-->-->-->(COE)-->--></p>
        </owl-collapse-panel>
        <owl-collapse-panel value="value3" header="">
          <p style="margin: 0; font-size: 14px;"></p>
        </owl-collapse-panel>
      </owl-collapse>
    </owl-code-example>
    <h3>Collapse </h3>
    <owl-table :data="propsCol" :columns="collapseCol" />
    <h3>Collapse Panel </h3>
    <owl-table :data="propsPanel" :columns="panelCol" />
  </div>
</template>

<script>
export default {
  data(){
    return {
      active: [],
      active1: ['value1'],
      code: `&lt;template&gt;
  <owl-collapse>
    <owl-collapse-panel value="value1" header="1">
    </owl-collapse-panel>
    <owl-collapse-panel value="value2" header="2">
    </owl-collapse-panel>
  </owl-collapse>
&lt;/template&gt;
`,
      code1:`&lt;template&gt;
  <owl-collapse v-model="active1">
    <owl-collapse-panel value="value1" header="">
      <p  style="margin: 0;font-size: 14px;"></p>
    </owl-collapse-panel>
    <owl-collapse-panel value="value2" header="">
      <p  style="margin: 0;font-size: 14px;">-->-->-->-->-->(COE)-->--></p>
    </owl-collapse-panel>
    <owl-collapse-panel value="value3" header="">
      <p style="margin: 0;font-size: 14px;"></p>
    </owl-collapse-panel>
  </owl-collapse>
&lt;/template&gt;
&lt;script&gt;
  export default {
    data(){
      return {
        active1: ['value1'],
      }
    }
  }
&lt;/script&gt;
      `,
      collapseCol: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      propsCol: [
        {name: 'expand', types: 'Boolean', default: 'false', introduce: ''},
        {name: 'value/v-model', types: 'string', default: '[]', introduce: ''},
      ],
      panelCol: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'introduce'},
      ],
      propsPanel: [
        {name: 'header', types: 'String', default: '', introduce: ''},
        {name: 'value', types: 'string', default: '', introduce: ''},
      ],
    }
  }
}
</script>

