<template functional>
  <div class="label-page">
    <h2>Label </h2>
    <h3></h3>
    <owl-code-example :code="code1">
      <owl-label
        height="42px"
        label=""
        ellipsis-bgm-color="#fff"
      />
    </owl-code-example>

    <h3></h3>
    <owl-code-example :code="code2">
      <div class="label2">
        <owl-label 
          height="42px"
          label=""
          ellipsis-color="var(--owl-success-color)"
          ellipsis-bgm-color="#f7e8e8"
        />
      </div>
    </owl-code-example>
    <h3></h3>
    <owl-table :data="list" :columns="columns" />
  </div>
</template>

<script>
export default {
  data(){
    return {
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'introduce'},
      ],
      list: [
        {name: 'height', types: 'string', introduce: ''},
        {name: 'label', types: 'string', introduce: ''},
        {name: 'ellipsisColor', types: 'string', introduce: ''},
        {name: 'ellipsisBgmColor', types: 'string', introduce: ''},
      ],
      code1:`&lt;template&gt;
  &lt;owl-label 
    height="42px" 
    label="" 
    ellipsis-bgm-color="#fff"
  /&gt;
&lt;/template&gt;`,
      code2:`&lt;style&gt;
.label1{
  background-color: #f7e8e8;
  padding: 20px;
}
.label1 .owl-label{
  color: var(--owl-primary-color);
}
&lt;/style&gt;
&lt;template&gt;
  &lt;div class="label1"&gt;
    &lt;owl-label 
      height="42px" 
      label="" 
      ellipsis-bgm-color="#fff"
      ellipsis-color="#f7e8e8"
    /&gt;
  &lt;/div&gt;   
&lt;/template&gt;`
    }
  }
}
</script>
<style lang="less">
.label-page{
  .label1{
    width: 420px;
    padding: 20px;
    background-color: #fff;
    font-size: 14px;
  }
  .owl-label{
    font-size: 14px;
  }
  .label2{
    padding: 4px 12px;
    background-color: #f7e8e8;
    .owl-label{
      color: var(--owl-success-color);
    }
  }
}
</style>
