<template>
  <div>
    <h2>Radio </h2>
    <h3></h3>
    <owl-code-example :code="code1">
      <p class="subtitle">?</p>
      <owl-radio name="code" :value="true" v-model="value"></owl-radio>
      <owl-radio name="code" :value="false" v-model="value"></owl-radio>
    </owl-code-example>
    <h3>Radio group</h3>
    <owl-code-example :code="code2">
      <p class="subtitle">?</p>
      <owl-radio-group v-model="value1">
        <owl-radio name="sport" :value="true"></owl-radio>
        <owl-radio name="sport" :value="false"></owl-radio>
      </owl-radio-group>
    </owl-code-example>
    <h3>Radio </h3>
    <owl-table :data="propsList" :columns="columns" />
    <h3>Radio group </h3>
    <owl-table :data="propsList1" :columns="columns" />
  </div>
</template>

<script>
export default {
  data(){
    return {
      value: '',
      value1: '',
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'introduce'},
      ],
      propsList: [
        {name: 'name', types: 'string', introduce: 'input'},
        {name: 'label', types: 'string', introduce: 'scopescope'},
        {name: 'v-model', types: 'string | number | boolean', introduce: ''},
        {name: 'value', types: 'string', introduce: 'inputradiovalue'},
      ],
      propsList1:[
        {name: 'v-model', types: 'string | number | boolean', introduce: ''},
        {name: 'direction', types: '"horizontal" | "vertical"', introduce: 'radio'},
      ],
      code1: `&lt;template&gt;
  <p>?</p>
  <owl-radio name="favorite" :value="true" v-model="value"></owl-radio>
  <owl-radio name="favorite" :value="false" v-model="value"></owl-radio>
&lt;/template&gt;`,
      code2: `&lt;template&gt;
  <p>?</p>
  <owl-radio-group v-model="value1">
    <owl-radio name="sport" :value="true"></owl-radio>
    <owl-radio name="sport" :value="false"></owl-radio>
  </owl-radio-group>
&lt;/template&gt;`
    }
  }
}
</script>