<template>
  <div class="modal-page">
    <h2>Modal </h2>
    <h3></h3>
    <owl-code-example :code="code">
      <div class="lookdetail1" @click="visible1 = true"></div>
    </owl-code-example>
    <h3>Slot</h3>
    <owl-code-example :code="code1">
      <div class="lookdetail1" @click="visible2 = true"></div>
    </owl-code-example>
    <h3></h3>
    <owl-table :data="list" :columns="columns" />
    <owl-modal :visible.sync="visible1"></owl-modal>
    <owl-modal :visible.sync="visible2">
      <div class="modal-body-custom"></div>
    </owl-modal>
  </div>
</template>

<script>
export default {
  data(){
    return {
      visible1: false,
      visible2: false,
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      list: [
        {name: 'visible', types: 'boolean', default: 'false', introduce: '.sync'},
        {name: 'shrink-menu', types: 'boolean', default: 'true', introduce: '""'},
        {name: 'middle', types: 'boolean', default: 'true', introduce: ''},
        {name: 'top', types: 'string', default: '200px', introduce: '200px'},
        {name: 'width', types: 'string', default: '680px', introduce: ''},
      ],
      code: `&lt;template&gt;
  <owl-modal :visible.sync="visible"></owl-modal>
&lt;/template&gt;

&lt;script&gt;
  export default {
    data(){
      return {
        visible1: false,
      }
    }
  }
&lt;/script&gt;
`,
      code1:`
&lt;template&gt;
  <owl-modal :visible.sync="visible1">
    <div class="modal-body-custom"></div>
  </owl-modal>
&lt;/template&gt;
`
    }
  }
}
</script>
<style lang="less">
.modal-page{
  .lookdetail1{
    cursor: pointer;
  }
}
.modal-body-custom{
  padding: 24px;
  text-align: center;
}
</style>