<template>
  <div>
    <h2>Button </h2>
    <h3></h3>
    <owl-code-example :code="code1">
      <p></p>
      <div style="font-size: 0">
        <owl-button size="small"></owl-button>
        <owl-button size="default"></owl-button>
        <owl-button size="large"></owl-button>
      </div>
      <p></p>
      <div style="font-size: 0">
        <owl-button type="primary"></owl-button>
        <owl-button type="success"></owl-button>
        <owl-button type="error" ></owl-button>
      </div>
      <p></p>
      <div style="font-size: 0">
        <owl-button type="primary" plain></owl-button>
        <owl-button type="success" plain></owl-button>
        <owl-button type="error" plain></owl-button>
      </div>
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code2">
      <owl-button search :loading="loading" @click="search"></owl-button>
      <br/>
      <br/>
      <owl-button >
        <span></span>
        <span class="suffix-icon owlfont owl-user" slot="suffix-icon"></span>
      </owl-button>
    </owl-code-example>
    <h3></h3>
    <owl-table :data="propsData" :columns="propsCol" />
  </div>
</template>

<script>
export default {
  data(){
    return {
      loading: false,
      propsCol: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      propsData: [
        {name: 'loading', types: 'boolean', default: 'false', introduce: 'loadingslotslot'},
        {name: 'search', types: 'boolean', default: 'false', introduce: 'loadingtrueloading'},
        {name: 'type', types: '"primary"|"success"|"error"', default: 'primary', introduce: ''},
        {name: 'plain', types: 'boolean', default: 'false', introduce: ''},
        {name: 'size', types: '"large"|"small"|"default"', default: 'default', introduce: ''},
      ],
      code1:`&lt;template&gt;
  <p></p>
  <div>
    <owl-button size="small"></owl-button>
    <owl-button size="default"></owl-button>
    <owl-button size="large"></owl-button>
  </div>
  <p></p>
  <div>
    <owl-button type="primary"></owl-button>
    <owl-button type="success"></owl-button>
    <owl-button type="error" ></owl-button>
  </div>
  <p></p>
  <div>
    <owl-button type="primary" plain></owl-button>
    <owl-button type="success" plain></owl-button>
    <owl-button type="error" plain></owl-button>
  </div>
&lt;/template&gt;`,
      code2:`&lt;template&gt;
  <owl-button search :loading="loading" @click="search"></owl-button>
  <br/>
  <br/>
  <owl-button @click="search">
    <span></span>
    <span class="suffix-icon owlfont owl-user" slot="suffix-icon"></span>
  </owl-button>
&lt;/template&gt;`
    }
  },
  methods: {
    search(){
      this.loading = true;
      setTimeout(()=>{
        this.loading = false;
      },1000)
    }
  }
}
</script>
