<template>
  <div>
    <h2>Date </h2>
    <h3></h3>
    <owl-code-example :code="code">
      <p class="subtitle">?</p>
      <owl-date v-model="value" />
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code1">
      <owl-date v-model="value1" value-format="YYYY/MM/DD" format="YYYY/MM/DD"></owl-date>
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code2">
      <owl-date v-model="value2" :quickly-choose="[
        {label: '2000-01-01', value: '2000-01-01'}, 
        {label: '2010-01-01', value: '2010-01-01'},
        {label: '2020-01-01', value: '2020-01-01'},
      ]" />
    </owl-code-example>
    <h3></h3>
    <owl-table :data="list" :columns="columns"></owl-table>
  </div>
</template>

<script>
export default {
  data(){
    return {
      value: '2022-04-01',
      value1: '2022-04-01',
      value2: '',
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      list: [
        {name: 'v-model', default: '', types: 'string', introduce: ''},
        {name: 'placeholder', types: 'string', default:' Please pick date', introduce: ''},
        {name: 'format', types: 'string', default:'YYYY-MM-DD', introduce: ''},
        {name: 'valueFormat', types: 'string', default:'YYYY-MM-DD', introduce: ''},
        {name: 'size', types: '"large"|"default"|"small"', default:'default', introduce: ''},
        {name: 'quicklyChoose', default: '[]', types: 'Array<{label: string, value: string}>', introduce: 'labelvalue'},
      ],
      code:`&lt;template&gt;
  &lt;owl-date v-model="value" /&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data(){
      return {
        value: '2022-04-01',
      }
    }
  }
&lt;/script&gt;`,
      code1: `&lt;template&gt;
  &lt;owl-date v-model="value1" value-format="YYYY/MM/DD" format="YYYY/MM/DD"/&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data(){
      return {
        value1: '2022-04-01',
      }
    }
  }
&lt;/script&gt;`,
      code2: `&lt;template&gt;
  <owl-date v-model="value2" :quickly-choose="[
    {label: '2000-01-01', value: '2000-01-01'}, 
    {label: '2010-01-01', value: '2010-01-01'},
    {label: '2020-01-01', value: '2020-01-01'},
  ]" />
&lt;/script&gt;`
    }
  }
}
</script>