<template>
  <div>
    <h2>Input </h2>
    <h3></h3>
    <owl-code-example :code="code1">
      <div class="input1">
        <div class="subt">Default</div>
        <owl-input
          class="input1-csm"
          placeholder=""
          v-model="input1"
        />
        <owl-input
          class="input1-csm"
          placeholder=""
          v-model="input2"
        />
      </div>
      <div class="input1">
        <div class="subt">Border</div>
        <owl-input
          border
          class="input1-csm"
          size="large"
          placeholder=""
          v-model="borderInput1"
        />
        <owl-input
          border
          class="input1-csm"
          placeholder=""
          v-model="borderInput2"
        />
        <owl-input
          border
          size="small"
          class="input1-csm"
          placeholder=""
          v-model="borderInput3"
        />
      </div>
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code2">
      <p class="subtitle"></p>
      <owl-input
        class="input2-csm"
        type="password"
        placeholder=""
        v-model="input3"
      />
      <p class="subtitle"></p>
      <owl-input
        class="input2-csm"
        type="number"
        placeholder=""
        v-model="inputNum"
      />
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code3" introduce="inputonblurrulevalidatefalse">
      <div class="input2">
        <div class="subt">Default</div>
        <owl-input
          class="input3-csm"
          placeholder=""
          v-model="input4"
          :rule="validate"
        />
      </div>
      <div class="input2">
        <div class="subt">Border</div>
        <owl-input
          border
          class="input3-csm"
          placeholder=""
          v-model="borderInput4"
          :rule="validate"
        />
      </div>
    </owl-code-example>
    <h3>Slots</h3>
    <owl-code-example :code="code4">
      <div class="input-account-wrap">
        <div class="input-account">
          <div class="title"></div>
          <owl-input placeholder="" v-model="userVal">
            <template slot="prefix"><span class="owlfont owl-user" style="font-size: 16px;"></span></template>
          </owl-input>
          <owl-input placeholder="" v-model="passwordVal" type="password">
            <template slot="prefix"><span class="owlfont owl-mima" style="font-size: 16px;"></span></template>
          </owl-input>
        </div>
        <div class="input-account">
          <div class="title"></div>
          <owl-input border placeholder="" v-model="searchVal">
            <template slot="suffix"><span class="owlfont owl-search suffix-icon"></span></template>
          </owl-input>
        </div>
      </div>
    </owl-code-example>
    <h3></h3>
    <owl-table :data="propsData" :columns="propsCol" />
  </div>
</template>
<script>
export default {
  data(){
    return{
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      input5: '',
      userVal: '',
      passwordVal: '',
      searchVal: '',
      borderInput1: '',
      borderInput2: '',
      borderInput3: '',
      borderInput4: '',
      
      inputNum: '',
      propsCol: [
        {title: '', key: 'name', width: '180px'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      propsData: [
        {name: 'value/v-model', types: 'string', default: '', introduce: ''},
        {name: 'type', types: '"text"|"password"|"number"', default:'text', introduce: ''},
        {name: 'border', types: 'boolean', default:'false', introduce: 'border'},
        {name: 'size', types: '"large"|"default"|"small"', default:'default', introduce: 'border'},
        {name: 'placeholder', types: 'string', introduce: 'placeholder'},
        {name: 'rule', types: 'Array<{require: boolean, tip: string, validate: (value)=>boolean}>', introduce: '"require""tip""validate"boolean'},
      ],
      validate: [
        {require: true, tip: ''},
        {require: true, tip: '11', validate:(val)=>{
          return /^\d{11}$/.test(val)
        }}
      ],
      code1:`&lt;template&gt;
  <div class="input1">
    <div class="subt">Default</div>
    <owl-input
      class="input1-csm"
      placeholder=""
      v-model="input1"
    />
    <owl-input
      class="input1-csm"
      placeholder=""
      v-model="input2"
    />
  </div>
  <div class="input1">
    <div class="subt">Border</div>
    <owl-input
      border
      class="input1-csm"
      size="large"
      placeholder=""
      v-model="borderInput1"
    />
    <owl-input
      border
      class="input1-csm"
      placeholder=""
      v-model="borderInput2"
    />
    <owl-input
      border
      size="small"
      class="input1-csm"
      placeholder=""
      v-model="borderInput3"
    />
  </div>
&lt;/template&gt;
`,
      code2:`&lt;template&gt;
  <p class="subtitle"></p>
  <owl-input
    class="input2-csm"
    type="password"
    placeholder=""
    v-model="input3"
  />
  <p class="subtitle"></p>
  <owl-input
    class="input2-csm"
    type="number"
    placeholder=""
    v-model="inputNum"
  />
&lt;/template&gt;
`,
      code3: `&lt;template&gt;
  <owl-input
    class="input2-csm"
    placeholder=""
    v-model="input4"
    :rule="validate"
  />
&lt;/template&gt;

&lt;script&gt;
export default {
  data(){
    return {
      input4: '',
      validate: [
        { require: true, tip: '' },
        { require: true, tip: '11', validate: (val) => {
          return /^\d{11}$/.test(val)
        } }
      ],
    }
  }
}
&lt;/script&gt; 
`,
      code4: `&lt;template&gt;
  <div class="input-account-wrap">
    <div class="input-account">
      <div class="title"></div>
      <owl-input placeholder="" v-model="userVal">
        <template slot="prefix">
          <span class="owlfont owl-user" style="font-size: 16px;"></span>
        </template>
      </owl-input>
      <owl-input placeholder="" v-model="passwordVal" type="password">
        <template slot="prefix">
          <span class="owlfont owl-mima" style="font-size: 16px;"></span>
        </template>
      </owl-input>
    </div>
    <div class="input-account">
      <div class="title"></div>
      <owl-input  placeholder="" v-model="searchVal">
        <template slot="suffix">
          <span class="owlfont owl-search"></span>
        </template>
      </owl-input>
    </div>
  </div>
&lt;/template&gt;
`,
      codeNumber: ``
    }
  }
}
</script>
<style lang="less">
.input1{
  width: 380px;
  border: 1px solid #e1e1e1;
  padding: 20px;
  height: 200px;
  display: inline-block;
  margin-right: 12px;
  vertical-align: top;
  .subt{
    color: #999;
    font-size: 14px;
  }
  &:last-child{
    margin-right: 0;
  }
  .owl-input.border{
    margin: 12px 0;
  }
}
.input2{
  display: inline-block;
  vertical-align: top;
  margin-right: 12px;
  &:last-child{
    margin-right: 0;
  }
  .subt{
    color: #999;
    font-size: 14px;
    margin-bottom: 12px;
  }
}
.input2-csm{
  width: 280px;
}
.input3-csm{
  width: 280px;
  display: inline-block;
  margin-right: 12px;
  vertical-align: top;
}
.input-account-wrap{
  display: flex;
}
.input-account{
  width: 380px;
  .title{
    font-size: 14px;
    color: #999;
    margin-bottom: 12px;
  }
  &:last-child{
    margin-left: 24px;
  }
}
</style>