<template>
  <div>
    <h2>Message </h2>
    <h3></h3>
    <owl-code-example :code="code1">
      <owl-button @click="showMessage('info')">info</owl-button>
      <br/><br/>
      <owl-button @click="showMessage('success')" type="success">success</owl-button>
      <br/><br/>
      <owl-button @click="showMessage('error')" type="error">error</owl-button>
      <br/><br/>
      <owl-button @click="showMessage('loading')">loading</owl-button>
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code2">
      <owl-button @click="showMessage1" search :loading="loading"></owl-button>
    </owl-code-example>
    <h3></h3>
    <p>key</p>
    <code>this.$message.info(content, key, duration)<br/>this.$message.info(content, key, duration)<br/>this.$message.error(content, key, duration)<br/>this.$message.loading(content, key, duration)</code>
    <h3></h3>
    <owl-table :data="list" :columns="columns" />
  </div>
</template>
<script>
export default {
  data(){
    return {
      loading: false,
      code1: `&lt;template&gt;
  <owl-button @click="showMessage('info')">info</owl-button>
  <br/><br/>
  <owl-button @click="showMessage('success')" type="success">success</owl-button>
  <br/><br/>
  <owl-button @click="showMessage('error')" type="error">error</owl-button>
  <br/><br/>
  <owl-button @click="showMessage('loading')">loading</owl-button>
&lt;/template&gt;
&lt;script&gt;
  export default {
    methods:{
      showMessage(type){
        this.$message({
          type,
          content: \`\${type}\`
        })
      },
    }
  }
&lt;/script&gt;`,
      code2:`&lt;template&gt;
  <owl-button @click="showMessage1" search :loading="loading"></owl-button>
&lt;/template&gt;
&lt;script&gt;
  export default {
    data(){
      return {
        loading: false
      }
    },
    methods:{
      showMessage1(){
        this.$message({
          type: 'loading',
          content: '...',
          duration: 0,
        }).then(res=>{
          this.loading = true;
          setTimeout(()=>{
            this.loading = false;
            res()
          }, 4500)
        })
      }
    }
  }
&lt;/script&gt; `,
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      list: [
        {name: 'params.key', types: 'string', default:'Date.now()', introduce: 'key'},
        {name: 'params.type', types: 'info|error|success|loading', default: 'info', introduce: ''},
        {name: 'params.content', types: 'string', introduce: ''},
        {name: 'params.duration', types: 'number', default: '2000', introduce: ''},
      ],
    }
  },
  methods: {
    showMessage(type){
      this.$message({
        type,
        content: `${type}`
      })
    },
    showMessage1(){
      this.$message({
        type: 'loading',
        content: '...',
        duration: 0,
      }).then(res=>{
        this.loading = true;
        setTimeout(()=>{
          this.loading = false;
          res()
        }, 4500)
      })
    }
  }
}
</script>