<template>
  <div>
    <h2> Popover</h2>
    <p>popoverpopoverpopover</p>
    <owl-code-example :code="code">
      <owl-popover trigger="click">
        <owl-button>click me</owl-button>
        <template slot="cover">
          <div class="owl-menu-item"></div>
          <div class="owl-menu-item"></div>
        </template>
      </owl-popover>
      <owl-popover trigger="hover">
        <owl-button>hover me</owl-button>
        <template slot="cover">
          <div class="owl-menu-item"></div>
          <div class="owl-menu-item"></div>
        </template>
      </owl-popover>
    </owl-code-example>
    <h3></h3>
    <owl-code-example :code="code1">
      <p class="subtitle"></p>
      <owl-input v-model="phone" placeholder="">
        <owl-popover text-arrow cover-arrow trigger="click" slot="prefix">
          <span>+{{region}}</span>
          <template slot="cover">
            <div class="owl-menu-item" @click="region = 86">+86</div>
            <div class="owl-menu-item" @click="region = 854">+854</div>
          </template>
        </owl-popover>
      </owl-input>
    </owl-code-example>
    <h3></h3>
    <owl-table :data="list" :columns="columns" />
  </div>
</template>

<script>
export default {
  data(){
    return {
      region: '86',
      code: `&lt;template&gt;
  <owl-popover trigger="click">
    <owl-button>click me</owl-button>
    <template slot="cover">
      <div class="owl-menu-item"></div>
      <div class="owl-menu-item"></div>
    </template>
  </owl-popover>
  <owl-popover trigger="hover">
    <owl-button>hover me</owl-button>
    <template slot="cover">
      <div class="owl-menu-item"></div>
      <div class="owl-menu-item"></div>
    </template>
  </owl-popover>
&lt;/template&gt;`,
      code1:`&lt;template&gt;
  <owl-input v-model="phone" placeholder="">
    <owl-popover text-arrow cover-arrow trigger="click" slot="prefix">
      <span>+86</span>
      <template slot="cover">
        <div class="owl-menu-item">+86</div>
        <div class="owl-menu-item">+854</div>
      </template>
    </owl-popover>
  </owl-input>
&lt;/template&gt;`,
      columns: [
        {title: '', key: 'name'},
        {title: '', key: 'types'},
        {title: '', key: 'default'},
        {title: '', key: 'introduce'},
      ],
      list: [
        {name: 'trriger', types: 'click|hover', default: 'click', introduce: ''},
        {name: 'textArrow', types: 'boolean', default: 'false', introduce: 'icon'},
        {name: 'coverArrow', types: 'boolean', default: 'false', introduce: 'arrow'},
      ],
    }
  }
}
</script>
<style>
.owl-popover{
  margin-right: 12px;
}
</style>